.faq {
  width: 100%;
  margin: 0 auto;
  padding: 136px 0 27px;
  box-sizing: border-box;
  background: url("../img/bg-faq_bag-desktop@1x.png") left 97% no-repeat,
    url("../img/bg-faq_watercolor-desktop@1x.png") 20% right no-repeat;
  background-size: 11.2%, 47.22%;

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background: url("../img/bg-faq_bag-desktop@2x.png") left 97% no-repeat,
      url("../img/bg-faq_watercolor-desktop@2x.png") 20% right no-repeat;
    background-size: 11.2%, 47.22%;
  }
}

.faq__wrapper {
  width: 100%;
  max-width: 1366px;
  margin: 0 auto;
  padding: 0 0 130px;
  box-sizing: border-box;
  background: url("../img/bg-faq_couple-desktop@1x.png") right top no-repeat;
  background-size: 47.22%;

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background: url("../img/bg-faq_couple-desktop@2x.png") right top no-repeat;
    background-size: 47.22%;
  }
}

.faq__main {
  max-width: 41%;
  margin: 0 auto 0 8.05%;
  padding: 0;
  box-sizing: border-box;
}

.faq h2 {
  margin: 0 0 8px;
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 40px;
  line-height: 49px;
  letter-spacing: -0.02em;
  text-transform: uppercase;
  color: #1fc0fc;
}

.faq ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

.faq li {
  margin: 0;
  padding: 24px 37px 23px 0;
  box-sizing: border-box;
  border-bottom: 1px solid rgba(72, 72, 72, 0.2);
}

.faq__link {
  display: flex;
  position: relative;
  align-items: baseline;
  width: 100%;
  margin: 0;
  padding: 0 25px 0 0;
  box-sizing: border-box;
  font-family: "Fira Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 17px;
  line-height: 25px;
  text-align: left;
  color: #484848;
  border: none;
  background-color: transparent;

  &::after {
    position: absolute;
    content: "";
    top: 25px;
    right: 0;
    width: 15px;
    height: 8px;
    background: url("../img/icon_faq_arrow_down.svg") center no-repeat;
  }

  &:hover,
  &:focus {
    opacity: 0.8;
    cursor: pointer;
  }

  &:active {
    opacity: 0.5;
  }
}


.faq__link--active {
  &::after {
    transform: rotate(180deg);
  }
}

.faq__link span:first-child {
  display: block;
  width: 13px;
  margin-right: 16px;
  font-style: normal;
  font-weight: bold;
  font-size: 28px;
  line-height: 48px;
  color: #ffc341;
}

.faq__main p {
  display: none;
  margin: 16px 0 7px;
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 21px;
  color: #484848;
  opacity: 0.74;
}

.faq__main .item-active {
  display: block;
}

.faq--nojs p {
  display: block;
}

@media (max-width: $tablet-width-only) {
  .faq {
    margin: 0 auto;
    padding: 81px 5% 40px;
  }

  .faq__wrapper {
    background: none;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background: none;
    }
  }

  .faq__main {
    max-width: 100%;
    margin: 0 auto;
  }

  .faq h2 {
    text-align: center;
  }
}

@media (max-width: $mobile-width-only) {
  .faq {
    background: url("../img/bg-faq_bag-mobile@1x.png") left 84% no-repeat, none;
    background-size: 45.6%;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background: url("../img/bg-faq_bag-mobile@2x.png") left 84% no-repeat, none;
      background-size: 45.6%;
    }
  }

  .faq__wrapper {
    width: 100%;
    padding: 0 0 40px;
  }

  .faq h2 {
    margin: 0;
    font-size: 32px;
    line-height: 39px;
  }

  .faq li {
    padding: 23px 0 15px;
  }

  .faq__link {
    padding: 0;
    font-size: 16px;
    line-height: 26px;
  }

  .faq__link span:first-child {
    margin-right: 12px;
  }

  .faq__link span:last-child {
    padding-right: 25px;
  }

  .faq__main p {
    margin: 18px 0 0;
    font-size: 14px;
    line-height: 22px;
  }
}
